import React, { useState } from 'react';
import logo from '../images/logo.png';
import { Link } from 'react-scroll';

function Navbar() {
    const [state, setState] = useState(false);

    const navBgColor = () => {
        window.scrollY >= 50 ? setState(true) : setState(false);
    };
    window.addEventListener('scroll', navBgColor);
    return (
        <nav className={state ? 'nav active' : 'nav'}>
            <Link to="main" className="logo">
                <img src={logo} alt="logo" smooth={true} duration={700} />
            </Link>
            {/* <input type="text" /> */}
            <ul className="menu">
                <li>
                    <Link to="main" smooth={true} duration={700}>
                        Home
                    </Link>
                </li>
                <li>
                    <Link to="features" smooth={true} duration={700}>
                        Features
                    </Link>
                </li>
                <li>
                    <Link to="presentaion" smooth={true} duration={700}>
                        Offer
                    </Link>
                </li>
                <li>
                    <Link to="aboutUsSection" smooth={true} duration={700}>
                        About US
                    </Link>
                </li>
                <li>
                    <Link to="contact" smooth={true} duration={700}>
                        Contact US
                    </Link>
                </li>
            </ul>
        </nav>
    );
}

export default Navbar;

