import React from 'react';
import FeatureComp from './FeatureComp';
import img1 from '../images/smallImg1.png';
import img2 from '../images/smallImg2.png';
import img3 from '../images/smallImg3.png';
import img4 from '../images/smallImg4.png';

function Feature() {
    return (
        <div id="features">
            <h1>FEATURES</h1>
            <span className="underline"></span>

            <div className="a-container">
                <FeatureComp image={img1} title="GENERAL FITNESS" />
                <FeatureComp image={img2} title="FITNESS FIRST" />
                <FeatureComp image={img3} title="TRY US" />
                <FeatureComp image={img4} title="RIDE TO FITNESS" />
            </div>
        </div>
    );
}

export default Feature;
