import React from 'react';
import aboutUsImg from '../images/img2.jpg';

function AboutUs() {
    return (
        <>
            <div className="aboutUsSection">
                <div className="aboutUs-image">
                    <img src={aboutUsImg} alt="img" />
                </div>
                <div className="about-text">
                    <h1>ABOUT US</h1>
                    <span className="underline" style={{ marginLeft: '5%' }}></span>

                    <p>
                        Lorem ipsum dolor sit amet consectetur, adipisicing elit. Perferendis eligendi culpa consectetur quis ipsum odio autem et, magnam quidem
                        qui fugit voluptatem aperiam laboriosam! Nisi dolore tempora ullam quis aliquid.
                    </p>
                    <button>Read More</button>
                </div>
            </div>
        </>
    );
}

export default AboutUs;
